import React, { Component } from "react";
import "./Styles/About.css";
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";

export default class About extends Component {
  componentDidMount() {
    document.body.style =
      "background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(0,104,55,1) 0%, rgba(11,51,33,1) 90% )";

    setTimeout(() => {
      document.querySelector(".next-page").style = "opacity:1";
    }, 1600);

    const vals = [80, 70, 99, 96, 90, 80];
    let pbars = document.querySelectorAll(".progress");
    setTimeout(() => {
      for (let i = 0; i < vals.length; i++) {
        pbars[i].setAttribute("value", vals[i]);
      }
    }, 1600);
  }
  render() {
    return (
      <section id="about-page">
        <Helmet>
          <title>develSanx-About</title>
        </Helmet>

        <div className="wraper">
          <div className="content">
            <h1 className="title">About Me</h1>
            <p>
              I viewed it as a time pass, then I realized it as an opportunity
              to show my skills, no matter how much time I spend with it, I can
              never ever cease learning new. I'm skilful in frameworks such as
              React, Angular, Vue, Django, Flask, Bootstrap, Bulma etc, and
              database development with Mysql, Sqlite, and MongoDB. I'm a
              passionate developer willing to take any kind of opportunity that
              will allow me to grow.
            </p>
          </div>

          <div className="skills">
            <span>Python</span>
            <progress className="progress is-primary" max="100"></progress>
            <span>Java</span>
            <progress className="progress is-link" max="100"></progress>
            <span>Html</span>
            <progress className="progress is-info" max="100"></progress>
            <span>Css</span>
            <progress className="progress is-success" max="100"></progress>
            <span>JavaScript</span>
            <progress className="progress is-warning" max="100"></progress>
            <span>Php</span>
            <progress className="progress is-danger" max="100"></progress>
          </div>

          <div className="next-page">
            <Link to="/works" className="button is-rounded">
              My Works
            </Link>
          </div>
        </div>
      </section>
    );
  }
}
