import React, { Component } from "react";
import "./Styles/Header.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";

export default class Header extends Component {
  state = {
    isActive: false
  };

  constructor(props) {
    super(props);
    this.triggerMenu = this.triggerMenu.bind(this);
  }

  render() {
    return (
      <header>
        <nav id="nav-bar">
          <div className="logo">
            <a href="https://develsanx.github.io/">
              <FontAwesomeIcon icon={faGithub} color="white"></FontAwesomeIcon>
              <span>develSanx</span>
            </a>
          </div>

          <div className="menus">
            <Link to="/">
              <h1 className="l1">Home</h1>
            </Link>
            <Link to="/about">
              <h1 className="l2">About</h1>
            </Link>
            <Link to="/works">
              <h1 className="l3">Works</h1>
            </Link>
            <Link to="/contact">
              <h1 className="l4">Contact</h1>
            </Link>
          </div>

          <div className="burger">
            <FontAwesomeIcon
              icon={faBars}
              color="white"
              className="burger-icon"
              onClick={this.triggerMenu}
            ></FontAwesomeIcon>

            <div className="fullMenu">
              <FontAwesomeIcon
                icon={faTimes}
                color="white"
                className="close-icon"
                onClick={this.triggerMenu}
              ></FontAwesomeIcon>

              <Link to="/" onClick={this.triggerMenu}>
                <h2>Home</h2>
              </Link>
              <Link to="/about" onClick={this.triggerMenu}>
                <h2>About</h2>
              </Link>
              <Link to="/works" onClick={this.triggerMenu}>
                <h2>Works</h2>
              </Link>
              <Link to="/contact" onClick={this.triggerMenu}>
                <h2>Contact</h2>
              </Link>
            </div>
          </div>
        </nav>
      </header>
    );
  }

  triggerMenu() {
    let currentStatus = this.state.isActive;
    if (!currentStatus) {
      document.querySelector(".fullMenu").className = "fullMenu menu-activated";
      this.setState({ isActive: true });
    } else {
      document.querySelector(".fullMenu").className = "fullMenu";
      this.setState({ isActive: false });
    }
  }
}
